import { Paper, Text, Stack, Group, Divider } from '@mantine/core';
import { LineChart } from '@mantine/charts';
import { IconTrendingUp } from '@tabler/icons-react';
import styles from './page.module.css';

const data = [
  { month: 'Янв', value: 1200000 },
  { month: 'Фев', value: 1250000 },
  { month: 'Мар', value: 1300000 },
  { month: 'Апр', value: 1280000 },
  { month: 'Май', value: 1350000 },
  { month: 'Июн', value: 1400000 },
];

export default function Analytics() {
  return (
    <div>
      <Paper shadow="lg" radius="lg" className={styles.balanceCard}>
        <div className={styles.balanceHeader}>
          <Group justify="space-between" align="center" className={styles.headerContent}>
            <Text className={styles.balanceLabel}>Баланс</Text>
            <Group gap={4}>
              <IconTrendingUp size={16} color="var(--mantine-color-green-4)" />
              <Text className={styles.growth}>+5.6% за месяц</Text>
            </Group>
          </Group>
        </div>

        <Divider color="var(--mantine-color-blue-7)" />

        <Stack gap="xs" p="md">
          <Text className={styles.balanceAmount}>1,250,000 ₽</Text>

          <LineChart
            h={180}
            data={data}
            dataKey="month"
            series={[{ name: 'value', color: 'var(--mantine-color-green-4)' }]}
            curveType="monotone"
            withDots={true}
            dotProps={{ r: 4 }}
            withLegend={false}
            withTooltip
            withXAxis
            withYAxis={false}
            gridAxis="none"
            strokeWidth={3}
          />
        </Stack>
      </Paper>
    </div>
  );
}
